
import PageContent from '@/components/page-content/src/page-content.vue'
import pageSearch from '@/components/page-search/src/page-search.vue'
import PageModal from '@/components/page-modal'
import { searchFormConfig } from './config/search.config'
import { contentTableConfig } from './config/content.cofig'
import { modalConfig } from './config/modal.config'
import { defineComponent, computed, ref, nextTick } from 'vue'
import { ElTree } from 'element-plus'

import { usePageModal } from '@/hook/use-page-modal'
import { useStore } from '@/store'
import { menuMapLeafKeys } from '@/utils/map-menus'

export default defineComponent({
  components: { pageSearch, PageContent, PageModal },
  name: 'role',
  setup() {
    const treeRef = ref<InstanceType<typeof ElTree>>()

    const editCallback = (item: any) => {
      // console.log('row数据', item)
      const leafKeys = menuMapLeafKeys(item.menuList)
      // console.log('leafKeys', leafKeys)
      // console.log('我treeRef', treeRef.value)
      nextTick(() => {
        treeRef.value?.setCheckedKeys(leafKeys, false)
      })
    }
    const { pageModalRef, defaultInfo, handleNewData, handleEditData } =
      usePageModal(undefined, editCallback)

    const tree = ref<InstanceType<typeof ElTree>>()
    console.log('tree', tree)

    const store = useStore()
    const menus = computed(() => store.state.entireMenu)

    const otherInfo = ref({})
    const handleCheckChange = (data1: any, data2: any) => {
      console.log(data1)
      console.log(data2)

      const checkedKeys = data2.checkedKeys
      const halfCheckedKeys = data2.halfCheckedKeys
      const menuList = [...checkedKeys, ...halfCheckedKeys]
      otherInfo.value = { menuList }
    }
    return {
      searchFormConfig,
      contentTableConfig,
      modalConfig,
      pageModalRef,
      defaultInfo,
      handleNewData,
      handleEditData,
      menus,
      otherInfo,
      handleCheckChange,
      treeRef
    }
  }
})
